
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#header {
    height: 50px;
    background: #c1ac38;
    line-height: 50px;
}
ul {
    margin: 0;
    padding: 10px;
}
body {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    background: #ccb42e;
}
li {
     list-style: none;
}
#total-money {
}
#total-money h3 img {
    height: 30px;
    position: absolute;
    top: 8px;
    left: -35px;
}
#total-money h3 {
    display: inline-block;
    font-weight: bold;
    margin: 0;
    position: relative;
    margin-left: 55px;
}
.miner img {
     height: 100px;
     display: inline-block;
}
.miner-info {
     display: inline-block;
     padding-bottom: 20px;
}
.miner-info p {
     position: relative;
     font-weight: bold;
     margin: 10px 0;
     margin-left: 25px;
}
.miner-info p img {
     height: 20px;
     position: absolute;
     top: -5px;
     left: -25px;
}
.miner-info button {
     padding: 5px;
     border-radius: 6px;
     border: none;
     border: 2px #217023 solid;
     background: #38c13c;
     color: #fff;
     font-weight: bold;
     font-size: 15px;
}
#add-miner p {
      margin: 0;
}
#add-miner.red {
    border: 5px red dotted;
    color: red;
}
#add-miner {
     border: none;
     background: transparent;
     height: 100px;
     width: 100%;
     line-height: 90px;
     text-align: center;
     color: #fff;
     border: 5px #fff dotted;
     font-size: 30px;
}
#add-miner:hover {
      background: rgba(255,255,255, .2);
      cursor: pointer;
}
#add-miner:hover p {
      display: none;
}
#add-miner:hover:before {
      content: '+';
      color: #fff;
      font-size: 60px;
      font-weight: bold;
      text-align: center;
      line-height: 90px;
}
